form .modal__submit-button {
  width: 100%;
  min-height: 45px;
  margin-bottom: 16px;
  padding-top: 6px;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.5px; }
  form .modal__submit-button:focus {
    outline: 2px solid orange; }

form .modal-form__agree-confirmation,
form .modal-form__mark {
  margin: 0; }

form .modal-form__label {
  position: relative;
  padding-left: 33px; }

form .modal-form__mark {
  position: absolute;
  left: 8px;
  top: 0;
  width: 15px;
  height: 15px;
  outline: 1px solid #ffffff;
  border-radius: 2px; }

form .modal-form__input-isagree:checked + span::after {
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url("/../img/svg/tick.svg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
  content: ''; }

form .modal-form__input-isagree:focus + span::after {
  outline: 2px solid orange; }

