  .page-header,
  .page-footer {
    flex-shrink: 0;
  }

  .page-main {
    flex-grow: 1;
  }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

:disabled {
  opacity: 0.2;
}
